.notice-detail{max-width:800px;margin:0 auto;padding:20px;background:#fff;border-radius:8px;box-shadow:0 2px 10px rgba(0,0,0,.05);text-align:left}.notice-detail .notice-header{padding-bottom:20px;border-bottom:1px solid #eee;margin-bottom:20px}.notice-detail .notice-header .notice-title{font-size:24px;margin:0 0 15px 0;color:#333}.notice-detail .notice-header .notice-meta{display:flex;gap:15px;font-size:14px;color:#666}.notice-detail .notice-header .notice-meta span{display:flex;align-items:center}.notice-detail .notice-header .notice-meta span::before{content:"|";margin-right:10px;color:#ddd}.notice-detail .notice-header .notice-meta span:first-child::before{content:none}.notice-detail .notice-content{line-height:1.8;padding:20px 0;min-height:200px}.notice-detail .notice-content p{margin:10px 0}.notice-detail .notice-content strong{color:#333}.notice-detail .notice-attachments{margin-top:30px;padding-top:20px;border-top:1px solid #eee}.notice-detail .notice-attachments h4{margin:0 0 15px 0;font-size:16px}.notice-detail .notice-attachments ul{list-style:none;padding:0;margin:0}.notice-detail .notice-attachments ul li{margin-bottom:8px}.notice-detail .notice-attachments ul li a{color:#0d6efd;text-decoration:none;display:inline-flex;align-items:center}.notice-detail .notice-attachments ul li a:hover{text-decoration:underline}.notice-detail .notice-attachments ul li a::before{content:"📎";margin-right:8px}.notice-detail .notice-footer{margin-top:30px;text-align:center}.notice-detail .notice-footer .list-button{display:inline-block;padding:10px 20px;background:#f8f9fa;color:#333;text-decoration:none;border-radius:4px;border:1px solid #ddd;transition:all .2s}.notice-detail .notice-footer .list-button:hover{background:#e9ecef}