.event-container{max-width:1200px;margin:0 auto;padding:20px;font-family:"Arial",sans-serif}.event-container .event-title{font-size:28px;font-weight:bold;margin-bottom:30px;color:#333}.event-container .main-slider{width:100%;height:400px;position:relative;margin-bottom:40px;border-radius:8px;overflow:hidden;box-shadow:0 4px 12px rgba(0,0,0,.1);cursor:pointer}.event-container .main-slider .slider-item{width:100%;height:100%;position:relative}.event-container .main-slider .main-slider-image{width:100%;height:100%;object-fit:cover;transition:transform .5s ease}.event-container .main-slider .slider-overlay{position:absolute;bottom:0;left:0;right:0;background:linear-gradient(to top, rgba(0, 0, 0, 0.7), transparent);color:#fff;padding:30px}.event-container .main-slider .slider-overlay .slider-title{font-size:2rem;margin-bottom:10px;font-weight:700;text-shadow:1px 1px 3px rgba(0,0,0,.5)}.event-container .main-slider .slider-overlay .slider-date{font-size:1.1rem;opacity:.9}.event-container .main-slider .slider-buttons{position:absolute;top:50%;width:96%;display:flex;justify-content:space-between;padding:0 20px}.event-container .main-slider .slider-buttons button{background:rgba(100,100,100,.3);color:#fff;border:none;width:50px;height:50px;border-radius:50%;cursor:pointer;font-size:20px;transition:all .3s;display:flex;align-items:center;justify-content:center;backdrop-filter:blur(5px)}.event-container .main-slider .slider-buttons button:hover{background:hsla(0,0%,100%,.7);color:#333;transform:scale(1.1)}.event-container .category-filter{margin-bottom:30px}.event-container .category-filter .category-tabs{display:flex;gap:10px;border-bottom:1px solid #eee;padding-bottom:15px}.event-container .category-filter .category-tabs .category-tab{padding:8px 16px;background:none;border:none;border-radius:20px;cursor:pointer;font-size:14px;transition:all .2s}.event-container .category-filter .category-tabs .category-tab:hover{background:#f5f5f5}.event-container .category-filter .category-tabs .category-tab.active{background:#333;color:#fff;font-weight:bold}.event-container .event-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));gap:20px;margin-bottom:40px}.event-container .event-grid .event-card{border:1px solid #eee;border-radius:8px;overflow:hidden;transition:transform .3s,box-shadow .3s;cursor:pointer}.event-container .event-grid .event-card:hover{transform:translateY(-5px);box-shadow:0 5px 15px rgba(0,0,0,.1)}.event-container .event-grid .event-card .event-cover{height:180px;position:relative;overflow:hidden}.event-container .event-grid .event-card .event-cover img{width:100%;height:100%;object-fit:cover;transition:transform .5s}.event-container .event-grid .event-card .event-cover:hover img{transform:scale(1.05)}.event-container .event-grid .event-card .event-cover .event-badge{position:absolute;top:10px;right:10px;padding:4px 12px;border-radius:12px;font-size:12px;font-weight:bold;color:#fff}.event-container .event-grid .event-card .event-cover .event-badge.진행중{background:#4caf50}.event-container .event-grid .event-card .event-cover .event-badge.종료됨{background:#f44336}.event-container .event-grid .event-card .event-cover .event-badge.예정{background:#2196f3}.event-container .event-grid .event-card .event-info{list-style-type:none;padding:15px;margin:0}.event-container .event-grid .event-card .event-info .event-title{font-size:16px;font-weight:bold;margin-bottom:8px;color:#333;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.event-container .event-grid .event-card .event-info .event-date{font-size:14px;color:#666}.event-container .page-navigation{display:flex;justify-content:center;gap:8px;margin-top:30px;flex-wrap:wrap}.event-container .page-navigation .page-button{padding:8px 16px;background:none;border:1px solid #ddd;border-radius:4px;cursor:pointer;font-size:14px;transition:all .2s;min-width:40px;text-align:center}.event-container .page-navigation .page-button:hover{background:#f5f5f5}.event-container .page-navigation .page-button.active{background:#333;color:#fff;border-color:#333;font-weight:bold}@media(max-width: 768px){.event-container{padding:15px}.event-container .main-slider{height:300px}.event-container .main-slider .slider-overlay{padding:20px}.event-container .main-slider .slider-overlay .slider-title{font-size:1.5rem}.event-container .event-grid{grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));gap:15px}}@media(max-width: 480px){.event-container .main-slider{height:250px}.event-container .main-slider .slider-overlay{padding:15px}.event-container .main-slider .slider-overlay .slider-title{font-size:1.2rem}.event-container .main-slider .slider-overlay .slider-date{font-size:.9rem}.event-container .main-slider .slider-buttons button{width:40px;height:40px;font-size:16px}.event-container .category-filter .category-tabs{flex-wrap:wrap}.event-container .event-grid{grid-template-columns:1fr}}